@font-face {
  font-face-name: "zCoolKuaiLe";
  src: url("../../assets/font/ZCoolKuaiLe.ttf");
}

.body {
  width: 100%;
  height: 100vh;
  background: #0D0E14;
}

.path {
  left: 25%;
  top: 10%;
  position: absolute;
  transform-style: preserve-3d;
  width: 48rem;
  height: 48rem;
  border-radius: 50%;
  animation: path-rotate 32s linear infinite;
}

.circleButton {
  position: absolute;
  display: flex;
  width: 10rem;
  height: 10rem;
  background: #D7CABC;
  mix-blend-mode: normal;
  border: 1px solid #777777;
  box-sizing: border-box;
  box-shadow: inset 10px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 78px;
  transform: rotate(-45deg) scaleY(5);
  animation: button-rotate 32s linear infinite;
  cursor: pointer;
}

.note {
  top: -5rem;
  left: calc(50% - 5rem);
}

.tool {
  top: calc(50% - 5rem);
  left: -5rem;
}

.article {
  top: calc(50% - 5rem);
  right: -5rem;
}

@keyframes path-rotate {
  0% {
    transform: scaleY(0.2) rotate(0);
  }
  100% {
    transform: scaleY(0.2) rotate(360deg);
  }
}

@keyframes button-rotate {
  0% {
    transform: rotate(360deg) scaleY(5);
  }
  100% {
    transform: rotate(0) scaleY(5);
  }
}

.entryActiveLabel {
  font-family: "zCoolKuaiLe", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 3rem;
  line-height: 3rem;
  align-items: center;
  text-align: center;
  letter-spacing: 0.255em;
  margin: auto;

  color: #0D0E14;
}

.entryLabel {
  font-family: "zCoolKuaiLe", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 2.25rem;
  line-height: 2.25rem;
  align-items: center;
  text-align: center;
  letter-spacing: 0.255em;
  margin: auto;
  color: #0D0E14;
}

.stars {
  position: relative;
  top: -20vh;
}

.star {
  position: relative;
  width: 8px;
  height: 8px;

  background: #C4C4C4;
  border: 2px solid #5BCFE9;
  box-sizing: border-box;
  filter: blur(4px);
  transform: matrix(-0.54, -0.83, 0.87, -0.52, 0, 0);
}

